// @require './style/index.scss'
import React, { PropTypes } from 'react';
import classnames from 'classnames';

class Icon extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const {
        prefixCls,
        type,
        className,
        ...other
    } = this.props;

    const cls = classnames({
      [prefixCls]: true,
      'icon-font': true,
      [`i-${type}`]: true,
      [className]: true
    });

    return (
      <i {...other} className={cls} />
    );
  }
}

Icon.propTypes = {
  prefixCls: PropTypes.string,
  type: PropTypes.string,
  className: PropTypes.string
};

Icon.defaultProps = {
  prefixCls: 'im-icon',
  type: '',
  className: ''
};


export default Icon;
